import React, { useEffect } from "react";
import { Link } from "react-router-dom";
import {gsap, Elastic} from "gsap";
import "./style.css";
import Social from "../../components/Social-icons";

export default function Header() {
    const tl = gsap.timeline();
    useEffect( ()=> {
        tl.from('header', {
            delay:4,
            duration:1,
            ease: Elastic.easeInOut,
            y:-150
        })
    }, []);
    return (
        <header>
            {/* museo sans TOP imperfecta BOTTOM*/}
            <img class="logo" src="img/Eskew-Consulting-Logo.png" alt="Eskew Consulting logo"/>
            <nav>
                <Link to="/">Link 1</Link>
                <Link to="/">Link 2</Link>
                <Link to="/">Link 3</Link>
                <Link to="/">Link 4</Link>
                <Link to="/">Link 5</Link>
            </nav>
            <Social />
        </header>
    )
}