import React, { Fragment, useEffect, useState } from "react";
import {gsap, Power3} from 'gsap';
import Content from "../../content/home";
import "./style.css";
import Art from "../../components/SVGS/art2";

export default function Intro() {
    const [content] = useState({
        ...Content
    });
    const title = content[0].introduction[0].title;
    useEffect( ()=>{
        const tl = gsap.timeline({defaults: {
            ease: Power3.easeOut
        }});
        tl.from('#intro', {
            delay:2,
            duration:2,
            y:'100vh'
        }).from('.hero1', {
            autoAlpha:0,
            width:'100%',
            duration:1
        }).to('.hero-pre', {
            display: 'none', 
            duration:.1, 
            delay:'-1'
        }).to('#intro',{
            backgroundColor: 'rgba(195,220,217,0)',
            duration:.25
        });
    },[])
    return (
        <Fragment>
            <img src="img/intro-placeholder.jpg" className="hero-pre" alt="Welcome to Eskew Consulting" />
            <section id="intro">
                <h1>
                    <span className="headline1">Self Trust</span> <span className="headline2">is a Must</span>
                </h1>
                <div className="hero1">
                    <img src="img/intro-placeholder.jpg" alt="" />
                    <div className="overlay"></div>
                </div>
            </section>
        </Fragment>
    )
}