import React, { useEffect, useState } from "react";
import Content from "../../content/home.json";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import "./style.css";
export default function Clients(){
    gsap.registerPlugin(ScrollTrigger);
    

    const [ content ] = useState({
        ...Content
    });

    const { title, client } = content[0].clients[0];

    let contestants = client.map( c =>
        <div className="client-container clearfix" id={c.id}>
            <img src={c.img} alt={c.name + " - " + c.competition} className="client-image" id={`${c.id}-img`} />
            <div className="client" id={`${c.id}-div`}>
                <h3>{c.name}</h3>
                <h4>{c.competition}</h4>
                <button onClick={()=> showQuote()}>See what she says about Brittney</button>
                <q className="testimonial">{c.testimonial}</q>
            </div>
            <img src={c.svg} alt="" className="clientSvg"/>
        </div>
    );
    function showQuote() {
        console.log('clicked');
    }
    useEffect(() => {
        const tlHeader = gsap.timeline({
            scrollTrigger: {
                trigger: '.clients-header',
                start: 'center center',
                end: 'bottom top',
                pin: true,
                pinSpacing: false,
                toggleActions: 'play none none reverse',
                scrub: true
            }
        })
        const imgTween = gsap.utils.toArray('.client-image');
        const textTween = gsap.utils.toArray('.client');

        tlHeader.fromTo('.clients-header', { autoAlpha:0 },{ autoAlpha: 1, duration: .25 }).to('.clients-header', { autoAlpha: 0, duration: .25 }, .5);

        imgTween.forEach( box => {
            let tlImage = gsap.timeline({
                scrollTrigger: {
                    trigger: box,
                    start: 'center center',
                    end: 'bottom top',
                    pin: true,
                    pinSpacing: false,
                    toggleActions: 'play none none reverse',
                    scrub: true
                }
            })
            tlImage.from(box, {
                autoAlpha: 0,
                duration:.25
            }).to(box, {
                autoAlpha: 0,
                duration: .25
            },.75);
        });
        textTween.forEach(box => {
            let tlText = gsap.timeline({
                scrollTrigger: {
                    trigger: box,
                    start: 'center center',
                    // end: 'center top',
                    end: '+=700',
                    pin: true,
                    toggleActions: 'play none none reverse',
                    scrub: true
                }
            });
            tlText.from(box, {
                duration: .4,
                autoAlpha: 0,
                y: 100  
            }).to(box, {
                duration: .2,
                autoAlpha: 0,
                y: -100
            }, .8);
        });
    }, []);
    return (
        <section id="clients" className="clients-grp clearfix fade">
            <h2 className="clients-header">{title}</h2>
            {contestants}
        </section>
    )
}