import React, { Fragment, useEffect, useState } from "react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

import './style.css';
import Content from "../../content/home";
import Art from "../../components/SVGS/art1";

export default function About() {
    gsap.registerPlugin(ScrollTrigger);
    const tl = gsap.timeline({ defaults: {
        duration: 1
    }});
    const [ content ] = useState({
        ...Content
    });
    let { title, bio, images } = content[0].about[0];

    useEffect( ()=> {
        tl.from('#about', {
            autoAlpha: 0,
            scrollTrigger: {
                trigger: '#about',
                start: 'top center+=200',
                end:'center center',
                scrub: true,
            }
        })
    })

    return (
        <Fragment>
            <p className="name-large">Brittney Eskew</p>
            <section id="about" className="clearfix fade">
                <img src={images[0].src} alt={images[0].alt} className="largerBio" />
                <img src={images[1].src} alt={images[1].alt} className="smallerBio" />
                <div className="bioText">
                    <h2>{title}</h2>
                    <p>{bio}</p>
                </div>
            </section>
        </Fragment>
    )
}