import React from "react";
import Floral from "../SVGS/floral";
import './style.css';
export default function Calendar() {
    let days = [];
    function calendar() {
        for (let i = 0; i < 31; i++) {
            days.push([i+1]);
        }
        return days.map( (num)=> <li></li>);
    }
    console.log(days);
    return (
        <section id="calendar" className="clearfix fade">
            <div className="schedule">
                <h2>Book a session</h2>
                <p>Select an option from below. We offer 3 options: in person, zoom, or a third option.</p>
                <a href="#" className="cta">In Person</a>
                <a href="#" className="cta">Zoom Call</a>
                <a href="#" className="cta">Third Option</a>
            </div>
            <ol className="calendarDays clearfix">
                {calendar()}
            </ol>  
        </section>
    )
}