import React, { useState, useRef } from "react";
import "./style.css";
import Content from "../../content/home";

export default function Questions(){
    const [content] = useState({
        ...Content
    });
    const [accordionState, setAccordionState] = useState("");
    let selected = useRef(null);
    let { title, questions } = content[0].questions[0];
    let array = [...questions];
    let questionList = array.map(element => {
        return (
            <li ref={el => { selected = el }} onClick={() => toggleClass(selected)} >
                <h3><span className="open"></span>{element.question}</h3>
                <div  className="accordion">
                    <p>{element.answer}</p>
                </div>
            </li>
        )
    });
    function toggleClass(el) {
        let slideState = (accordionState === 'expand') ? '' : 'expand';
        setAccordionState(slideState);
        el.className = slideState;
    }
    return (
        <section id="questions">
            <h2>{title}</h2>
            <ul>
                {questionList}
            </ul>
        </section>
    )
}